import React from 'react';
import {getListSelector} from "../selectors";
import {Link} from "react-router-dom";
import { useSelector } from 'react-redux'

const List = () => {
  const list = useSelector(getListSelector);

  return (
    <>
      <ul className="mb-3">
        {list.map(item => (
          <li key={item.id}>
            <Link to={`/vacancies/${item.id}`}>{item.position}</Link>
          </li>
        ))}
      </ul>

      <h4 className="h4">Какие условия мы предлагаем нашим сотрудникам:</h4>
      <ul>
        <li>Высокая и стабильная заработная плата;</li>
        <li>Работа над интересными задачами и проектами;</li>
        <li>Карьерный рост и развитие внутри СКФУ.</li>
      </ul>
      <p>Присылайте свое резюме на почту управления кадрового администрирования <a href="mailto:hr@ncfu.ru">hr@ncfu.ru</a></p>
    </>
  )
};

export default List;
