import React, {useEffect} from 'react';
//import {  useHistory } from 'react-router-dom';
import {Operation} from "../reducer";
import {getItemSelector, getIsLoadingSelector} from "../selectors";
import {Link} from "react-router-dom";
import { useDispatch, useSelector } from 'react-redux'

const Item = (props) => {
  // const history = useHistory();
  const dispatch = useDispatch();
  const item = useSelector(getItemSelector);
  const isLoading = useSelector(getIsLoadingSelector);

  useEffect(() => {
    dispatch(Operation.loadItem(props.match.params.id));
  }, []);


  const {position, features, responsibility, contacts} = item;

  if (isLoading) return null;

  return (
    <div className="pt-4 pb-4">
      <h1 className="h4 mb-3">{position}</h1>
      <p className="h5">Чем занимается {position}</p>
      <div dangerouslySetInnerHTML={{__html: responsibility}}></div>

      <p className="h5">Мы ищем кандидата</p>
      <div dangerouslySetInnerHTML={{__html: features}}></div>
      {(contacts) && <div className="mb-4">
        <p className="h5">Контакты</p>
        {contacts.map(contact => (
          <div className="mb-3">
            <p className="mb-0" dangerouslySetInnerHTML={{__html: contact.name}}></p>
            <p className="mb-0" dangerouslySetInnerHTML={{__html: contact.pos}}></p>
            <p className="mb-0" dangerouslySetInnerHTML={{__html: contact.tel}}></p>
          </div>
        ))}

        <p>Присылайте свое резюме на почту управления кадрового администрирования <a href="mailto:hr@ncfu.ru">hr@ncfu.ru</a></p>
      </div>
      }

      <Link to={`/`}>Вернуться к списку вакансий</Link>
      {/*<button onClick={history.goBack} className="btn btn-primary">Вернуться</button>*/}
    </div>
  )
};

export default Item;
